.displayweather {
  margin-top: 20px;
  display: flex;
  flex-flow: column nowrap;
}

.displayweather .maincard {
  width: 500px;
  margin: 0px 20px;
  align-self: center;
  padding: 50px;
  position: relative;
}

.displayweather .maincard .cardtitle {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 1.5rem;
}

.displayweather .maincard .cardsubtitle {
  position: absolute;
  left: 10px;
  color: grey;
  margin-top: -8px;
}
.displayweather h1 {
  font-size: 5rem;
  margin: -10px;
  margin-left: -5%;
}

.displayweather .weather-icon {
  width: 100px;
  position: absolute;
  left: 50%;
  top: 50px;
}

.displayweather .weather-main {
  position: absolute;
  top: 141px;
  left: 325px;
  font-weight: 500;
  font-size: 1.2rem;
}

.displayweather .weather-description {
  position: absolute;
  left: 28px;
  font-size: 1.4rem;
}
.displayweather .weatherdetails {
  margin-left: 50px;
  margin-right: 50px;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  grid-gap: 20px;
}
.displayweather .weatherdetails .section1 {
  justify-self: right;
  width: 300px;
}

h4 {
  text-align: left;
  margin-right: 30px;
  font-size: 1.2rem;
  margin-top: 18px;
}

.displayweather .weatherdetails span {
  position: relative;
  font-size: 1.2rem;
  top: 7px;
}

@media (max-width: 500px) {
  .displayweather {
    overflow: hidden;
    flex-flow: wrap;
    justify-content: flex-start;
    width: 90vw;
    margin: 0;
  }

  .displayweather .maincard {
    width: 90vw;
    border-radius: 0px;
    left: -40px;
    border: none;
    background: none;
  }

  .displayweather .weatherdetails {
    margin-left: 0px;
    display: block;
  }

  h4 {
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .displayweather .weatherdetails span {
    position: relative;
    font-size: 1.2rem;
    top: 0px;
  }

  .weather-icon {
    display: none;
  }
  .displayweather .maincard .cardtitle {
    left: 50px;
  }
  .displayweather .maincard .cardsubtitle {
    left: 50px;
  }

  .displayweather .weather-description {
    left: 50px;
  }
  .displayweather .weather-main {
    display: none;
  }
  .displayweather h1 {
    margin-left: -2%;
  }
}
